.home-right-col{

  padding-right: 0px!important;

  .home-right-img{

    height: 100%;
    background-position: center!important;
    background-size: cover!important;
    background-repeat: no-repeat!important;

  }

}

.main-home-band{

  background: white;

  .home-left-col{

    padding-bottom: 40px;

    p{

      margin-bottom: 125px;

    }

  }

}

.second-home-band-wrapper{

  @include paddingTopBot();

}

.products-home-band-wrapper{

  @include paddingTopBot();
  background: $lightest-blue;

  h2{

    padding-top: 10px;

  }

}

.home-products-row{

  position: relative;
  height: 375px;

  @media(max-width: $medium){

    height: 325px;

  }

  @media(max-width: $mobile){

    height: auto;

  }

  .product-link-wrapper{

    position: absolute;
    bottom: 20px;

    @media(max-width: $mobile){

      position: relative;
      margin-top: 40px;
      margin-bottom: 40px;


    }

  }

}


.home-product-wrapper{

  h3{

    padding-top: 15px;
    padding-bottom: 8px;
    font-size: 25px;


    @media(max-width: $medium){

      font-size: 20px;

    }

  }

  a{

    color: $green;
    padding-top: 10px;
    padding-bottom: 10px;
    font-weight: bold;

  }


  .feat-img-wrapper{

    background-position: center!important;
    background-size: cover!important;
    background-repeat: no-repeat!important;
    height: 200px;
    transition: $transition;

    &:hover{

      opacity: 0.8;

    }

    @media(max-width: $medium){

      height: 175px;

    }

    @media(max-width: $mobile){

      height: 350px;

    }

  }

}


.home-market-wrapper{

  background: $light-green;

  @media(max-width: $mobile){

    margin-bottom: 25px;

  }

  .markets-link-wrapper{

    @include centerBackgroundImage();
    background-image: url('http://localhost/sentek/wp-content/themes/sentek/images/markets-home-background.jpg');

    h3{

      padding-top: 15px;
      padding-bottom: 15px;
      font-size: 24px;
      text-align: center;


      @media(max-width: $medium){

        font-size: 20px;

      }

    }

    a{

      color: white;
      padding-top: 10px;
      padding-bottom: 10px;
      text-align: center;
      text-transform: uppercase;
      font-weight: 300;

    }


  }




  .feat-img-wrapper{

    background-position: center!important;
    background-size: cover!important;
    background-repeat: no-repeat!important;
    height: 225px;
    transition: $transition;

    &:hover{

      opacity: 0.8;

    }

    @media(max-width: $medium){

      height: 175px;

    }

  }

}

