    }

  }

  .icon-wrapper{

      padding: 40px;

    }

    .strength-col{

      padding-bottom: 25px;

    }

    .strength-col-1{

      h4{

        margin-top: -62px;

        @media(max-width: $mobile){

          margin-top: auto;

        }

      }

    }

    .strength-col-2{

      background: white;

    }

    .strength-col-3{

      h4{

        margin-top: -46px;

        @media(max-width: $mobile){

          margin-top: auto;

        }

      }

    }

    .strength-col-4{

      background: white;

    }

    .strength-col-5{

      background: $lightest-green;

      @media(max-width: $mobile){

        background: white;

      }

      h4{

        margin-top: -65px;

        @media(max-width: $mobile){

          margin-top: auto;

        }

      }

    }

    .strength-col-6{

      h4{

        margin-top: -31px;

        @media(max-width: $mobile){

          margin-top: auto;

        }

      }

    }

    h4{

      color: $green;
      border-bottom: 1px solid $light-blue;
      padding-top: 50px;
      padding-bottom: 15px;
      margin-bottom: 25px;
      font-size: 19px;

    }

}

.leaders-wrapper{

    background: $light-green;

    .fourth-home-band{

      @include paddingTopBot();
      color: white;


      h4{

        font-size: 36px;
        padding-bottom: 10px;
        border-bottom: 2px solid white;
        margin-bottom: 20px;

      }

      p{

        padding-bottom: 20px;
        font-size: 18px;

      }


    .simplecarousal{

      padding-left: 30px;
      padding-right: 30px;

      /*button.slick-next.slick-arrow{

        background: rgba(255,255,255,0.0)!important;

      }*/

    }

    .feat-img-container{

      @include centerBackgroundImage();
      height: 225px;

    }

    .home-news-col{

      h5{

        padding-bottom: 30px;

      }

      a{

        color: white;

      }

    }

  }

}

.bottom-wrapper{

  padding-bottom: 25px;

  .gform_description{

    font-size: 18px!important;

  }

}

#gform_1{

    input{

      height: 55px!important;
      color: $light-blue!important;

      &::placeholder{

          color: $light-blue!important;

        }

      }


      .gform_heading{

      text-align: center;

      h3{

        font-size: 30px!important;
        padding-bottom:25px;
        margin-bottom: 20px;
        padding-top: 25px;
        color: $light-blue!important;
        border-bottom: 2px solid $light-blue;

      }

    }

    .gform_button{

      position: relative !important;
      margin-left: 32% !important;
      padding-left: 50px;
      padding-right: 50px;
      text-transform: uppercase!important;
      background: $light-blue!important;
      color: white!important;
      border: 1px white!important;
      transition: $transition;

      &:hover{

        border: 1px solid $light-blue!important;
        background: white!important;
        color: $light-blue!important;

      }

      @media(max-width: $laptop){

        margin-left: 30% !important;

      }

      @media(max-width: $medium){

        margin-left: 0% !important;

      }

    }

}

